"use client";

import { useState, useEffect } from "react";
import LeadCreationForm from "./LeadCreationForm";

interface User {
  id: number;
  username: string;
  status: string;
  role: string;
}

export default function LeadAssignmentButton() {
  const [users, setUsers] = useState<User[]>([]);
  const [showModal, setShowModal] = useState(false);
  const [selectedUserId, setSelectedUserId] = useState<number | null>(null);
  const [showForm, setShowForm] = useState(false);

  useEffect(() => {
    async function fetchUsers() {
      const res = await fetch("/api/users");
      const data = await res.json();
      // Filter available salespeople (role: sales, status: available)
      const availableSales = data.filter(
        (u: User) => u.role === "sales" && u.status === "available"
      );
      setUsers(availableSales);
    }
    if (showModal) {
      fetchUsers();
    }
  }, [showModal]);

  function handleUserSelect(e: React.ChangeEvent<HTMLSelectElement>) {
    setSelectedUserId(Number(e.target.value));
  }

  function handleAssignClick() {
    if (selectedUserId !== null) {
      setShowForm(true);
    }
  }

  function handleFormClose() {
    setShowForm(false);
    setShowModal(false);
    setSelectedUserId(null);
  }

  return (
    <>
      <button
        className="bg-black text-white px-4 py-2 rounded hover:bg-gray-800"
        onClick={() => setShowModal(true)}
      >
        Asignar Lead
      </button>

      {showModal && !showForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white text-black rounded p-6 w-96">
            <h2 className="text-xl font-bold mb-4">Seleccione Comercial Disponible</h2>
            <select
              className="w-full border border-gray-300 rounded p-2 mb-4"
              value={selectedUserId ?? ""}
              onChange={handleUserSelect}
            >
              <option value="" disabled>
                Seleccione un comercial
              </option>
              {users.map((user) => (
                <option key={user.id} value={user.id}>
                  {user.username} - {user.status}
                </option>
              ))}
            </select>
            <div className="flex justify-end space-x-2">
              <button
                className="px-4 py-2 bg-gray-300 rounded hover:bg-gray-400"
                onClick={() => setShowModal(false)}
              >
                Cancelar
              </button>
              <button
                className="px-4 py-2 bg-black text-white rounded hover:bg-gray-800 disabled:opacity-50"
                onClick={handleAssignClick}
                disabled={selectedUserId === null}
              >
                Siguiente
              </button>
            </div>
          </div>
        </div>
      )}

      {showForm && selectedUserId !== null && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 overflow-auto p-4">
          <div className="bg-white text-black rounded p-6 max-w-lg w-full">
            <h2 className="text-xl font-bold mb-4">Crear Lead para Comercial</h2>
            <LeadCreationForm
              onLeadCreated={() => {
                handleFormClose();
                alert("Lead creado exitosamente");
              }}
              assignedTo={selectedUserId}
            />
            <div className="flex justify-end mt-4">
              <button
                className="px-4 py-2 bg-gray-300 rounded hover:bg-gray-400"
                onClick={handleFormClose}
              >
                Cancelar
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
