"use client";

import { useState, useEffect } from "react";

interface Lead {
  id: number;
  name: string;
  company: string;
  email: string;
  phone: string;
  project_type: string;
  city: string;
  metraje: string;
  assigned_to_username: string;
  status: string;
  quote_value: number;
  product_line: string;
  price_list: string;
  comments: string;
  document_path: string | null;
}

const columns = [
  { id: "new", title: "New", color: "bg-gray-200" },
  { id: "qualification", title: "Qualification", color: "bg-red-200" },
  { id: "presentation", title: "Presentation", color: "bg-yellow-200" },
  { id: "negotiation", title: "Negotiation", color: "bg-green-200" },
  { id: "evaluation", title: "Evaluation", color: "bg-purple-200" },
];

export default function KanbanBoard() {
  const [leads, setLeads] = useState<Lead[]>([]);

  useEffect(() => {
    fetch("/api/leads")
      .then((res) => res.json())
      .then((data) => setLeads(data));
  }, []);

  function leadsByStatus(statusId: string) {
    if (statusId === "qualification") {
      // Map statuses that belong to qualification
      return leads.filter((lead) => lead.status === "qualification");
    }
    if (statusId === "presentation") {
      return leads.filter((lead) => lead.status === "presentation");
    }
    if (statusId === "negotiation") {
      return leads.filter((lead) => lead.status === "negotiation");
    }
    if (statusId === "evaluation") {
      return leads.filter((lead) => lead.status === "evaluation");
    }
    // Default to new
    return leads.filter((lead) => lead.status === "new");
  }

  return (
    <div className="flex space-x-4 overflow-x-auto p-4">
      {columns.map((col) => (
        <div key={col.id} className={`flex-shrink-0 w-72 rounded shadow ${col.color} p-4`}>
          <h3 className="font-bold mb-4">{col.title}</h3>
          <div className="space-y-3 max-h-[600px] overflow-y-auto">
            {leadsByStatus(col.id).map((lead) => (
              <div key={lead.id} className="bg-white rounded p-3 shadow cursor-pointer">
                <h4 className="font-semibold">{lead.name}</h4>
                <p className="text-sm text-gray-600">{lead.company}</p>
                <p className="text-xs text-gray-500">Comercial: {lead.assigned_to_username || "Sin asignar"}</p>
                <p className="text-xs text-gray-500">Estado: {lead.status}</p>
                <p className="text-xs text-gray-500">Valor: ${lead.quote_value?.toLocaleString() || 0}</p>
              </div>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
}
