"use client";

import { useState } from "react";

interface LeadCreationFormProps {
  onLeadCreated: () => void;
  assignedTo: number;
}

export default function LeadCreationForm({ onLeadCreated, assignedTo }: LeadCreationFormProps) {
  const [formData, setFormData] = useState({
    name: "",
    company: "",
    email: "",
    phone: "",
    project_type: "",
    city: "",
    metraje: "",
    lead_type: "",
  });

  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);

  function handleChange(e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  }

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setError(null);
    setSuccess(null);

    const payload = {
      ...formData,
      assigned_to: assignedTo,
    };

    const res = await fetch("/api/leads", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(payload),
    });

    if (res.status === 409) {
      const data = await res.json();
      setError(`Cliente ya existe y está asignado a: ${data.assignedTo}`);
    } else if (res.ok) {
      setSuccess("Lead creado exitosamente");
      setFormData({
        name: "",
        company: "",
        email: "",
        phone: "",
        project_type: "",
        city: "",
        metraje: "",
        lead_type: "",
      });
      onLeadCreated();
    } else {
      setError("Error al crear el lead");
    }
  }

  return (
    <form onSubmit={handleSubmit} className="max-w-lg space-y-4 p-4 border border-gray-300 rounded">
      <h2 className="text-xl font-bold mb-4">Crear Lead</h2>
      {error && <p className="text-red-600">{error}</p>}
      {success && <p className="text-green-600">{success}</p>}
      <input
        type="text"
        name="name"
        placeholder="Nombre"
        value={formData.name}
        onChange={handleChange}
        required
        className="w-full border border-gray-300 rounded p-2"
      />
      <input
        type="text"
        name="company"
        placeholder="Empresa"
        value={formData.company}
        onChange={handleChange}
        className="w-full border border-gray-300 rounded p-2"
      />
      <input
        type="email"
        name="email"
        placeholder="Correo"
        value={formData.email}
        onChange={handleChange}
        required
        className="w-full border border-gray-300 rounded p-2"
      />
      <input
        type="tel"
        name="phone"
        placeholder="Número"
        value={formData.phone}
        onChange={handleChange}
        required
        className="w-full border border-gray-300 rounded p-2"
      />
      <input
        type="text"
        name="project_type"
        placeholder="Tipo de Proyecto"
        value={formData.project_type}
        onChange={handleChange}
        className="w-full border border-gray-300 rounded p-2"
      />
      <input
        type="text"
        name="city"
        placeholder="Ciudad"
        value={formData.city}
        onChange={handleChange}
        className="w-full border border-gray-300 rounded p-2"
      />
      <input
        type="text"
        name="metraje"
        placeholder="Metraje"
        value={formData.metraje}
        onChange={handleChange}
        className="w-full border border-gray-300 rounded p-2"
      />
      <input
        type="text"
        name="lead_type"
        placeholder="Tipo de Lead (Instagram, WhatsApp, etc.)"
        value={formData.lead_type}
        onChange={handleChange}
        className="w-full border border-gray-300 rounded p-2"
      />
      <button
        type="submit"
        className="bg-black text-white px-4 py-2 rounded hover:bg-gray-800"
      >
        Crear Lead
      </button>
    </form>
  );
}
